@import url(https://fonts.googleapis.com/css2?family=Francois+One&display=swap);
html{background:#121322}body{margin:0}.controls{display:flex;justify-content:center;align-items:center;flex-direction:column;width:100vw;height:100vh}.control{width:80%;text-align:center;margin:10px;background:#1e1945;color:rgba(255,255,255,0.75);border:solid rgba(255,255,255,0.75) 3px;font-family:'Francois One', sans-serif;font-size:400%;cursor:pointer}@media (hover: hover){.control:hover{background:#fff;color:#1e1945;border-color:#1e1945}.control:active{transform:scale(0.9)}}@media (hover: none){.control:hover{-webkit-animation-name:flash-white;animation-name:flash-white;-webkit-animation-duration:3s;animation-duration:3s}}@-webkit-keyframes flash-white{0%{background:#1e1945;color:rgba(255,255,255,0.75);border-color:rgba(255,255,255,0.75)}1%{background:#fff;color:#1e1945;border-color:#1e1945}5%{background:#fff;color:#1e1945;border-color:#1e1945}6%{background:#1e1945;color:rgba(255,255,255,0.75);border-color:rgba(255,255,255,0.75)}}@keyframes flash-white{0%{background:#1e1945;color:rgba(255,255,255,0.75);border-color:rgba(255,255,255,0.75)}1%{background:#fff;color:#1e1945;border-color:#1e1945}5%{background:#fff;color:#1e1945;border-color:#1e1945}6%{background:#1e1945;color:rgba(255,255,255,0.75);border-color:rgba(255,255,255,0.75)}}.noselect{-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}

